.navigation{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: $nav_height;
    position: fixed; top: 0%; left: 0%; right: 0%; bottom: 0%;
    background-color: rgba(255, 255, 255, 1);
    @if $is_phone_portrait{
        padding: 0px 20px 0px 20px;
        font-size: 27px;
        background-color: rgba(255, 255, 255, 1);
    }@else if $is_phone_landscape{
        padding: 0px 20px 0px 20px;
        font-size: 20px;
    }@else{
        padding: 0px 50px 0px 50px;
        background-color: rgba(255, 255, 255, 0);
    };
    z-index: 999;
    
    .ham-menu{
        @if $is_phone_portrait{
            position: absolute;
            right: 25px;
            display: flex;
        }
    }

    .buttons-selection{
        @if $is_phone_portrait{
            display: none;
        }@else{
            display: flex;
        }

        .menu-option{
            padding-right: 20px;

            $svg_width: 112.5%;


            .option{
                cursor: pointer;

                span{
                    color: rgba(26, 27, 31, 0.25);
                    font-weight: 600;
                }
            }

            .svg-animation{
                width: 0px;
                transition: .5s;

                width: $svg_width;
                height: 7.5%;
                position: relative; right: 6.25%;
                border-radius: 7px;
            }

            #is-selected{
                background-color: rgba(26, 27, 31, 0.24);
            }


            .option:hover ~ .svg-animation{
                background-color: rgba(255, 255, 255, 0);
                transition: .5s;
                background-color: rgba(26, 27, 31, 0.1);
            }
        }

        #is-last-option{
            padding-right: 0
        }
    }
}